<template>
  <div>
    <b-row>
      <b-button @click="goToClientProcess" variant="primary" class="mb-3 ml-2">{{
        $t("BACK")
      }}</b-button>
    </b-row>
    <b-row>
      <div class="title-fw title-fs-2 ml-5">
        {{ $t("CUSTOMERINFORMATION") }}
      </div>
      <br /><br />
    </b-row>
    <!-- 顧客情報 -->
    <div class="ml-5">
      <b-row>
        <b-col xl="5">
          <b-row class="d-flex align-items-center">
            <b-col xl="4">
              <label class="highLightHeaderLabel">{{
                $t("COMPANYNAMEORNAME")
              }}</label>
            </b-col>
            <b-col>
              <b-form-input
                v-model="basicinfo.companyName"
                class="form-control"
                disabled
              ></b-form-input>
            </b-col>
          </b-row>
        </b-col>
        <b-col xl="3">
          <b-row class="d-flex align-items-center">
            <b-col xl="auto">
              <label class="highLightHeaderLabel">{{
                $t("Headquarterslocation")
              }}</label>
            </b-col>
            <b-col>
              <b-form-input
                v-model="basicinfo.officeAddress"
                class="form-control"
                value="北海道"
                disabled
              ></b-form-input>
            </b-col>
          </b-row>
        </b-col>
        <b-col xl="3">
          <b-row class="d-flex align-items-center">
            <b-col xl="auto">
              <label class="highLightHeaderLabel">{{ $t("CONTACT") }}</label>
            </b-col>
            <b-col>
              <b-form-input
                v-model="basicinfo.personIncharge"
                class="form-control"
                disabled
              ></b-form-input>
            </b-col>
          </b-row>
        </b-col>
      </b-row>
      <br />
      <b-row>
        <b-col xl="5">
          <b-row class="d-flex align-items-center">
            <b-col xl="4">
              <label class="highLightHeaderLabel">{{ $t("PHONENO") }}</label>
            </b-col>
            <b-col>
              <b-form-input
                v-model="basicinfo.phoneNo"
                class="form-control"
                disabled
              ></b-form-input>
            </b-col>
          </b-row>
        </b-col>
        <b-col xl="5">
          <b-row class="d-flex align-items-center">
            <b-col xl="auto">
              <label class="highLightHeaderLabel">{{
                $t("MAILADDRESS")
              }}</label>
            </b-col>
            <b-col>
              <b-form-input
                v-model="basicinfo.email"
                class="form-control"
                value=""
                disabled
              ></b-form-input>
            </b-col>
          </b-row>
        </b-col>
      </b-row>
    </div>

    <!-- 補助金詳細 -->
    <hr class="hrType" />
    <b-row>
      <div class="title-fw title-fs-2 ml-5">{{ $t("SUBSIDYDETAIL") }}</div>
      <br /><br />
    </b-row>
    <b-button @click="gotoSubsidyInfoEdit" variant="primary" class="ml-5">{{
      $t("SUBSIDYASSESSMENTEDIT")
    }}</b-button>
    <br /><br />
    <div class="ml-5">
      <b-row>
        <b-col xl="10">
          <b-row class="d-flex align-items-center">
            <b-col xl="2">
              <label class="highLightHeaderLabel">{{
                $t("SUBSIDYGRANTJUDGEMENTRESULT")
              }}</label>
            </b-col>
            <b-col>
              <b-form-input
                v-model="subsidyinfo.judgementResult"
                class="form-control"
                value="補助金・助成金の申請が通る可能性が高いです"
                disabled
              ></b-form-input>
            </b-col>
          </b-row>
        </b-col>
      </b-row>
      <br />
      <b-row>
        <b-col xl="10">
          <b-row class="d-flex align-items-center">
            <b-col xl="2">
              <label class="highLightHeaderLabel">{{ $t("SITUATION") }}</label>
            </b-col>
            <b-col xl="4">
              <b-form-input
                v-model="subsidyinfo.status"
                class="form-control"
                value="査定中"
                disabled
              ></b-form-input>
            </b-col>
          </b-row>
        </b-col>
      </b-row>
      <br />
      <b-row>
        <b-col xl="10">
          <b-row class="d-flex align-items-center">
            <b-col xl="2">
              <label class="highLightHeaderLabel">{{
                $t("ASSESSMENTREQUESTDATE")
              }}</label>
            </b-col>
            <b-col xl="4">
              <b-form-input
                v-model="subsidyinfo.requestDate"
                class="form-control"
                value="2020/7/23"
                disabled
              ></b-form-input>
            </b-col>
            <b-col xl="auto">
              <label class="highLightHeaderLabel">{{
                $t("APPLICATIONDATE2")
              }}</label>
            </b-col>
            <b-col xl="4">
              <b-form-input
                v-model="subsidyinfo.applicationDate"
                class="form-control"
                value="-"
                disabled
              ></b-form-input>
            </b-col>
          </b-row>
        </b-col>
      </b-row>
      <br />
      <b-row>
        <b-col xl="10">
          <b-row class="d-flex align-items-center">
            <b-col xl="2">
              <label class="highLightHeaderLabel">{{ $t("REMARKS") }}</label>
            </b-col>
            <b-col>
              <b-form-input
                v-model="subsidyinfo.remarks"
                class="form-control"
                value="〇〇〇〇〇〇〇〇〇補助金の査定"
                disabled
              ></b-form-input>
            </b-col>
          </b-row>
        </b-col>
      </b-row>
      <br />
      <b-row>
        <b-col xl="10">
          <b-row class="d-flex align-items-center">
            <b-col xl="2">
              <label class="highLightHeaderLabel">{{ $t("CONTACT") }}</label>
            </b-col>
            <b-col xl="4">
              <b-form-input
                v-model="subsidyinfo.personIncharge"
                class="form-control"
                value="山田太郎"
                disabled
              ></b-form-input>
            </b-col>
          </b-row>
        </b-col>
      </b-row>
    </div>

    <!-- 対応履歴 -->
    <hr class="hrType" />
    <b-row>
      <div class="title-fw title-fs-2 ml-5">
        {{ $t("CORRESPONDENCEHISTORY") }}
      </div>
      <br /><br />
    </b-row>
    <b-button @click="onTransactAdd" variant="primary" class="ml-5">{{
      $t("ADDCUSTOMERSERVICE")
    }}</b-button>
    <br /><br />
    <MessageComponent :messageList="MessageList"></MessageComponent>
    <br /><br />
  </div>
</template>

<script>
import MessageComponent from "@/components/MessageComponent";
import { Settings } from "luxon";
Settings.defaultLocale = "ja";
import { mapGetters } from "vuex";
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import { Config } from "@/shared/config";
import { endPoint } from "@/shared/endPoint";

export default {
  components: { MessageComponent },
  data() {
    return {
      basicinfo: {
        companyName: "",
        personIncharge: "",
        phoneNo: "",
        email: "",
        officeAddress: "",
      },
      subsidyinfo: {
        judgementResult: "",
        status: "",
        requestDate: "",
        remarks: "",
        applicationDate: "",
        personIncharge: "",
      },
      MessageList: [],
      appUrl: Config.apiBaseUrl,
      order_by: "DESC",
    };
  },
  computed: {
    ...mapGetters({
      assessment_Details: "getAssesmentNo",
      client_details: "getClientData",
    }),
  },
  mounted() {
    this.subsidyAssesesmentData();
    this.getMessageInfo();
  },
  methods: {
    /**
     * @description function to fetch the clientData and subsiday data
     * @name subsidyAssesesmentData
     */
    async subsidyAssesesmentData() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.CLIENTREADCLIENTSUBSIDYASSESSMENTDETAIL,
        {
          client_no: this.client_details.client_no,
          assessment_no: this.assessment_Details.assesmentno,
        }
      );
      if (data.success) {
        let responseData = data.data.clientBaseInfoObject;
        let subsidyData = data.data.subsidyAssessentObject;
        let personData = data.data.personObject;
        let officeData = data.data.generalPurposeObject;
        let ResultData = data.data.subsidyChartRObject;
        this.basicinfo.companyName = responseData.client_name;
        this.basicinfo.personIncharge = responseData.client_person_name;
        this.basicinfo.phoneNo = responseData.client_tel;
        this.basicinfo.email = responseData.client_mail;
        this.basicinfo.officeAddress = officeData.value_string_small;
        //subsidyInfo
        let SubSidyType;
        if (subsidyData.assessment_sts_kbn == "01") {
          SubSidyType = this.$t("SUBSIDY.ASSESSMENTREQUEST");
        } else if (subsidyData.assessment_sts_kbn == "02") {
          SubSidyType = this.$t("SUBSIDY.UNDERASSESSMENT");
        } else if (subsidyData.assessment_sts_kbn == "03") {
          SubSidyType = this.$t("SUBSIDY.APPLYING");
        } else if (subsidyData.assessment_sts_kbn == "04") {
          SubSidyType = this.$t("SUBSIDY.APPLIED");
        } else {
          SubSidyType = "";
        }
        if (subsidyData.assessment_dt != null) {
          let Dt = new Date(subsidyData.assessment_dt);
          let AssesesMentDate =
            Dt.getFullYear() +
            "/" +
            this.getYearMonth(parseInt(Dt.getMonth() + 1).toString()) +
            "/" +
            this.getYearMonth(Dt.getDate().toString());
          this.subsidyinfo.requestDate = AssesesMentDate;
        }
        if (subsidyData.request_dt != null) {
          let Dt2 = new Date(subsidyData.request_dt);
          let assesmentRequestdate =
            Dt2.getFullYear() +
            "/" +
            this.getYearMonth(parseInt(Dt2.getMonth() + 1).toString()) +
            "/" +
            this.getYearMonth(Dt2.getDate().toString());
          this.subsidyinfo.applicationDate = assesmentRequestdate;
        } else {
          this.subsidyinfo.applicationDate = "-";
        }
        this.subsidyinfo.judgementResult = ResultData.judge_result;
        this.subsidyinfo.status = SubSidyType;
        this.subsidyinfo.remarks = subsidyData.remarks;
        if (personData != null) {
          this.subsidyinfo.personIncharge = personData.person_name;
        }
        //subsidyInfo
      }
    },
    /**
     * @description function to format the month and year
     * @name getYearMonth
     */
    getYearMonth(item) {
      let data = "";
      if (item.slice(0, 2).length == 1) {
        data += "0" + item.slice(0, 2);
      } else {
        data += item.slice(0, 2);
      }
      return data;
    },
    /**
     * @description function to go back previous page
     * @name goToClientProcess
     */
    goToClientProcess() {
      if (this.assessment_Details.routeFlag) {
        this.$router.push({ name: "task_subsidy_details" });
      } else {
        this.$router.push({ name: "client_process" });
      }
      this.$store.dispatch("setClientData", {
        client_name: this.client_details.client_name,
        client_no: this.client_details.client_no,
        tab_index: 7,
      });
    },
    /**
     * @description function to call the subsidyInfoEdit page
     * @name getsubsidyInfoEdit
     */
    gotoSubsidyInfoEdit() {
      this.$router.push({ name: "client_mgmt_subsidy_info_edit" });
    },
    /**
     * @description function to fetch the message details
     * @name getMessageInfo
     */
    async getMessageInfo() {
      let Messages = [];
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADCLIENTCOMMUNICATION,
        {
          client_no: this.client_details.client_no,
          assess_req_no : this.assessment_Details.assesmentno,
          order_by: this.order_by,
        }
      );
      if (data.success) {
        let responseData = data.data.communicationListObject;
        responseData.forEach((item) => {
          let MessageDate = new Date(item.message_dt);
          MessageDate = new Date(
            MessageDate - MessageDate.getTimezoneOffset() * 1000 * 60
          );
          let MessageMonth = MessageDate.getMonth() + 1;
          let Day = MessageDate.getDate();
          let Time =
            ("0" + MessageDate.getHours()).slice(-2) +
            ":" +
            ("0" + MessageDate.getMinutes()).slice(-2);

          let iconPath = "";
          if (
            item.message_sender_icon_path &&
            item.message_sender_icon_path != "undefined" &&
            item.message_sender_icon_path != "null"
          ) {
            iconPath = this.appUrl + item.message_sender_icon_path;
          }
          let messageData = {
            messageNo: item.message_no,
            attached_file_data: item.attachments,
            senderBelongs: item.message_sender_belongs,
            personName: item.message_sender_person_name,
            messageTitle: item.message_title,
            morningMessage: item.message_text,
            IconPath: iconPath,
            AttchMentFlag: item.attached_file_flg,
            message_sender_account_type: item.message_sender_account_type,
            SupportType: item.client_support_type,
            Month: MessageMonth,
            Day: Day,
            Time: Time,
          };
          Messages.push(messageData);
        });
        this.MessageList = Messages;
        console.log("Messages", this.MessageList);
      }
    },
    /**
     * @description function to call the clientTransaction page
     * @name onTransactAdd
     */
    onTransactAdd() {
      this.$store.dispatch("setQuotationNo", {
        Quotation_no: "",
        Proposal_no: "",
        Supplier : "",
        supplierNo : ""
      });
      this.$router.push({ name: "client_mgmt_client_transaction_add" });
    },
  },
};
</script>

<style>
.hrType {
  border-width: 10;
  border-top: 1px dotted gray;
  border-bottom: 1px dotted gray;
}
</style>