<template>
  <div>
    <div class="border border-dark pl-4 pr-4">
      <div v-for="message in messageList" :key="message.messageNo">
        <div>
          <b-row class="d-flex align-items-center">
            <b-col xl="auto">
              {{ message.Month }}{{ $t("MONTHJP") }}{{ message.Day
              }}{{ $t("SUNDAY") }}{{ $t("WOOD") }}
            </b-col>
            <b-col>
              <hr class="hrType" />
            </b-col>
          </b-row>

          <b-row>
            <b-col v-if="message.message_sender_account_type != '001'" xl="6" >
            </b-col>
            <b-col
              v-if="message.message_sender_account_type != '001'"
              xl="auto"
              class="pr-0"
            >
              {{ message.Time }}
            </b-col>
            <b-col>
              <div class="border border-dark pl-2 pr-2">
                <template v-if="message.IconPath">
                  <img
                    v-if="message.IconPath"
                    v-bind:src="message.IconPath"
                    class="imageSize float-left mt-2"
                  />
                </template>
                <template v-else>
                  <i
                    class="fa fa-user fa-2x float-left mt-1"
                    aria-hidden="true"
                  ></i>
                </template>
                <div
                  v-if="message.SupportType == '001'"
                  class="float-right pr-1 mt-2"
                >
                  <i class="fa fa-phone-square" aria-hidden="true"></i>
                </div>
                <div
                  v-if="message.SupportType == '003'"
                  class="float-right pr-1 mt-2"
                >
                  <i class="fa fa-envelope" aria-hidden="true"></i>
                </div>
                <div
                  v-if="message.SupportType == '002'"
                  class="float-right pr-1 mt-2"
                >
                  <i class="fa fa-fax" aria-hidden="true"></i>
                </div>
                <div
                  v-if="message.SupportType == '004'"
                  class="float-right pr-1 mt-2"
                >
                  <i class="fa fa-user float-right" aria-hidden="true"></i>
                </div>
                <div
                  v-if="message.SupportType == '005'"
                  class="float-right pr-1 mt-2"
                >
                  <i class="fa fa-handshake-o" aria-hidden="true"></i>
                </div>
                <div
                  v-if="message.SupportType == '006'"
                  class="float-right pr-1 mt-2"
                >
                  <i class="fa fa-paper-plane" aria-hidden="true"></i>
                </div>

                <label class="ml-2 mt-2">{{ message.senderBelongs }}</label
                >： <label>{{ message.personName }}</label
                >&nbsp;&nbsp;
                <label>{{ message.messageTitle }} </label>
                <div v-if="message.AttchMentFlag" class="float-right mt-2 pr-1" >
                  <i class="fa fa-paperclip" aria-hidden="true"></i>
                </div>
              </div>
              <div
                class="border border-dark pl-2 pr-2 align card-content-height"
              >
                <span v-html="message.morningMessage"></span>
              </div>
              <div
                v-if="message.AttchMentFlag"
                class="border border-dark pl-2 pr-2"
              >
                <div
                  v-for="(item, i) in message.attached_file_data"
                  v-bind:key="i"
                >
                  <b-link :href="appUrl + item.attached_file_path" download>
                    <u>{{ item.attached_file_name }}</u>
                  </b-link>
                </div>
              </div>
            </b-col>
            <b-col
              v-if="message.message_sender_account_type == '001'"
              xl="auto"
              class="pr-0"
            >
              {{ message.Time }}
            </b-col>
            <b-col v-if="message.message_sender_account_type == '001'" xl="6">
            </b-col>
          </b-row>
          <br />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
import { Config } from "@/shared/config";

export default {
  name: "SubMenuTree",
  components: {},
  inheritAttrs: false,
  props: {
    messageList: {
      type: [Array],
      default: () => [],
    },
  },
  data: () => {
    return {
      appUrl: Config.apiBaseUrl,

      selected: "",
    };
  },
  computed: {
    ...mapGetters({}),
  },
  mounted() {},
  created() {},
  methods: {},
};
</script>
<style>
.imageSize {
  height: 20px;
  width: 20px;
}
.card-content-height {
  min-height: 7rem;
}
</style>